/*=============================================*/

/*---------------------------------------------
    Кнопки
---------------------------------------------*/

.btn {
  min-width: 86px;
  padding: 6px 27px 8px;
  text-align: center;
  border: none;
  border-radius: 20px;
  background: #2e8b42;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  line-height: 100%;
  font-weight: 400;
  color: #ffffff;
  transition: opacity ease 0.5s;
  cursor: pointer;
  appearance: none;
}

.btn:hover {
  opacity: 0.85;
}

.btn-icon {
  min-width: 86px;
  padding: 20px 28px;
  text-align: center;
  border: none;
  background: transparent;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  color: #444444;
  transition: opacity ease 0.5s;
  cursor: pointer;
  appearance: none;
}

.btn-icon svg {
  display: inline-block;
  vertical-align: middle;
}

.btn-submit {
  min-width: 200px;
  padding: 12px 58px;
  text-align: center;
  border: none;
  border-radius: 5px;
  background: #2e8b42;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 100%;
  font-weight: 500;
  color: #f4f4f4;
  text-transform: uppercase;
  transition: opacity ease 0.5s;
  cursor: pointer;
  appearance: none;
}

.btn-submit:hover {
  opacity: 0.85;
}

.btn-submit--suscribe {
  width: 100%;
  font-size: 14px;
}

.btn-submit--suscribe-footer {
  width: 100%;
  background: #3a884b;
  font-size: 18px;
}

.btn-add-recept {
  min-width: 180px;
  padding: 6px 36px 7px;
  text-align: center;
  border: 1px solid #2e8b42;
  border-radius: 20px;
  background: #e5e5e5;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: #2e8b42;
  transition: opacity ease 0.5s;
  cursor: pointer;
  appearance: none;
}

.btn-add-recept:hover {
  opacity: 0.85;
}

.btn-ingridients {
  display: inline-block;
  vertical-align: middle;
  border: none;
  background: transparent;
  cursor: pointer;
  appearance: none;
}

.btn-close {
  border: none;
  background: none;
}

.btn-close span {
  position: relative;
  display: block;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.btn-close span:hover::before,
.btn-close span:hover::after {
  background: #111111;
}

.btn-close span::before,
.btn-close span::after {
  content: '';
  position: absolute;
  top: 10px;
  display: block;
  width: 20px;
  height: 3px;
  background: #777777;
  transition: background-color 0.5s ease;
}

.btn-close span::before {
  transform: rotate(45deg);
}

.btn-close span::after {
  transform: rotate(-45deg);
}

