.title-section {
  margin: 0;
  text-align: center;
  font-family: Days, Arial, sans-serif;
  font-size: 32px;
  line-height: 41px;
  color: #444444;
}

.title-section-inner {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 24px;
  line-height: 100%;
  font-weight: 500;
  color: #444444;
}

.title-recept {
  margin: 0;
  font-family: Days, Arial, sans-serif;
  font-size: 20px;
  line-height: 25px;
  color: #444444;
}

.title-sub {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 20px;
