/*---------------------------------------------
  Секция slider
---------------------------------------------*/

.slider {
  position: relative;
}

.slider__item {
  min-height: 452px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.title-site {
  margin: 0;
  padding: 102px 0 0 162px;
  font-family: Days, Arial, sans-serif;
  font-size: 80px;
  line-height: 102px;
  color: #f4f4f4;
  letter-spacing: 0.08em;
}

.title-site span {
  display: block;
  font-family: Days, Arial, sans-serif;
  font-size: 48px;
  line-height: 1;
  color: #f4f4f4;
  letter-spacing: 0;
}

.container-controls {
  position: absolute;
  z-index: 11;
  top: 49%;
  left: 50%;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1358px;
  margin: 0 auto;
  padding: 0 20px;
  transform: translateX(-50%);
}

.owl-carousel .owl-stage {
  display: flex;
  align-items: stretch;
}

.btn-control {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background: rgba(68, 68, 68, 0.6);
  transition: background 0.3s ease-in;
  cursor: pointer;
}

.btn-control svg {
  width: 10px;
  height: 20px;
  fill: rgba(68, 68, 68, 0.6);
  stroke: #ffffff;
  stroke-width: 3px;
}

.btn-control:hover {
  background: rgba(68, 68, 68, 0.2);
}

.btn-control:hover svg {
  fill: rgba(68, 68, 68, 0.2);
}

.owl-carousel .owl-dots {
  position: absolute;
  z-index: 10;
  left: 50%;
  bottom: 20px;
  width: 100%;
  text-align: center;
  transform: translateX(-50%);
}

.owl-carousel .owl-dots .owl-dot {
  display: inline-block;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background: rgba(68, 68, 68, 0.6);
  transition: all ease 0.5s;
}

.owl-carousel .owl-dots .owl-dot.active {
  background: #444444;
}

.owl-carousel .owl-dots .owl-dot:not(:first-child) {
  margin-left: 24px;
}

