/*---------------------------------------------
  Секция recept-review
---------------------------------------------*/

.recept-review {
  margin-top: 35px;
  padding-top: 100px;
  background: #ffffff;
}

.recept-review__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -17.5px;
  margin-right: -17.5px;
  justify-content: space-between;
  margin-top: 100px;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция ingridients
---------------------------------------------*/

.ingridients {
  padding-top: 100px;
  background: #ffffff;
}

.ingridients__content {
  display: flex;
  justify-content: center;
}

.ingridients__list-left,
.ingridients__list-right {
  list-style-type: none;
}

.ingridients__list-left {
  padding: 40px 107px 0 0;
}

.ingridients__list-right {
  padding: 120px 0 0 130px;
}

.ingridients__item-left,
.ingridients__item-right {
  position: relative;
  margin-bottom: 125px;
  vertical-align: middle;
  color: rgba(68, 68, 68, 0.6);
}

.ingridients__item-left svg,
.ingridients__item-right svg {
  width: 20px;
  height: 20px;
  fill: transparent;
  display: inline-block;
  vertical-align: middle;
  stroke: #444444;
  stroke-width: 4px;
}

.ingridients__item-left {
  text-align: left;
}

.ingridients__item-left:after {
  content: '';
  position: absolute;
  z-index: 10;
  left: 0;
  bottom: -20px;
  width: 340px;
  height: 3px;
  background: #2e8b42;
}

.ingridients__item-right {
  text-align: right;
}

.ingridients__item-right::before {
  content: '';
  position: absolute;
  z-index: 10;
  right: 0;
  bottom: -20px;
  width: 400px;
  height: 3px;
  background: #2e8b42;
}

.ingridients img {
  background: #f4f4f4;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.25);
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция  avtor
---------------------------------------------*/

.avtor {
  padding: 160px 0 120px;
  background: #ffffff;
}

.avtor__content {
