}

.avtor__img {
  width: 265px;
  height: 350px;
  border: 1px solid rgba(68, 68, 68, 0.2);
  border-radius: 5px;
}

.avtor__desc {
  max-width: 755px;
  margin-left: 100px;
}

.avtor__info {
  margin-top: 42px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 20px;
  line-height: 132.19%;
  color: rgba(68, 68, 68, 0.6);
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция  recept-other
---------------------------------------------*/

.recept-other {
  padding: 80px 0;
  border-top: 1px solid rgba(68, 68, 68, 0.2);
  border-bottom: 1px solid rgba(68, 68, 68, 0.2);
  background: #f4f4f4;
}

.recept-other__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -17.5px;
  margin-right: -17.5px;
  justify-content: space-between;
}

.recept-other__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 17.5px;
  margin-right: 17.5px;
  width: calc(23.75% - 35px);
  border: 1px solid rgba(68, 68, 68, 0.2);
  border-radius: 5px;
}

.recept-other__item img {
  display: block;
  width: 100%;
  height: auto;
}

.recept-other__desc {
  padding: 12px;
  background: #ffffff;
}

.recept-other__desc p {
  margin-bottom: 20px;
  text-align: center;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 132.19%;
  color: rgba(68, 68, 68, 0.6);
}

/*=============================================*/

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 15;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.7);
  visibility: hidden;
}

.modal.visial {
  visibility: visible;
}

.modal__dialog {
  position: relative;
  max-width: 500px;
  margin: 40px auto;
}

.modal__dialog.open {
  animation-name: animatetop;
  animation-duration: 0.7s;
}

.modal__dialog.close {
  animation-name: animatedown;
  animation-duration: 0.5s;
}

.modal__btn-close {
  position: absolute;
  top: 15px;
  right: 20px;
}

.modal__content {
  padding: 15px;
  width: 100%;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  max-height: 80vh;
  overflow-y: auto;
}

.modal__body {
  padding: 15px;
}

.modal__footer {
  padding: 15px;
}

@keyframes animatetop {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes animatedown {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0);
  }
}

/*=============================================
        ПОДВАЛ
=============================================*/

.footer {
  position: relative;
}

.footer__topline {
  padding: 32px 0 44px;
  background: #444444;
}

.footer__content {
  display: flex;
  justify-content: space-between;
}

.footer__logo {
