}

.email__input:-moz-placeholder {
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 100%;
  color: #c4c4c4;
}

.email__input::-moz-placeholder {
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 100%;
  color: #c4c4c4;
}

.email__input:-ms-input-placeholder {
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 100%;
  color: #c4c4c4;
}

.checkbox-footer {
  font-family: Roboto, Arial, sans-serif;
  font-size: 15px;
  line-height: 170%;
  color: #c4c4c4;
  cursor: pointer;
}

.checkbox-footer__input {
  position: absolute;
  opacity: 0;
}

.checkbox-footer__fake {
  position: relative;
  top: 5px;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 12px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  background: transparent;
}

.checkbox-footer__fake::after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  display: inline-block;
  width: 11px;
  height: 6px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  color: #c4c4c4;
  transform: rotate(-45deg);
}

.checkbox-footer__fake::after {
  content: none;
}

.checkbox-footer__input:checked + .checkbox-footer__fake::after {
  content: '';
}

.checkbox-footer__input:focus + .checkbox-footer__fake {
  box-shadow: 0 0 0 0.1em pink;
}

.nav-footer {
  margin-bottom: 65px;
}

.nav-footer__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style-type: none;
}

.nav-footer__link {
  display: inline-block;
  font-family: Roboto, Arial, sans-serif;
  font-size: 19px;
  line-height: 22px;
  color: #c4c4c4;
  transition: color ease-in 0.3s;
}

.nav-footer__link:hover {
  color: #eaeaea;
}

.socials__title {
  margin-top: 0;
  margin-bottom: 32px;
  text-align: center;
  font-family: Roboto, Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  font-weight: 500;
  color: #c4c4c4;
}

.socials__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.socials__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 3px;
}

.socials__link {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.socials__link svg {
  width: 25px;
  height: 20px;
  fill: #ffffff;
  transition: transform ease-in 0.3s;
}

.socials__link--ws svg {
  fill: transparent;
  stroke-width: 2px;
  stroke: #ffffff;
}

.socials__link:hover svg {
  transform: scale(1.4);
}

.footer-copy {
  padding: 15px 0;
  background: #f4f4f4;
}

.footer-copy__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-copy__info {
  font-family: Days, Arial, sans-serif;
  font-size: 18px;
  line-height: 23px;
  color: rgba(68, 68, 68, 0.6);
}

.footer-copy__metrics {
  display: flex;
  justify-content: space-between;
}

.footer-copy__count {
  padding: 0 10px;
  border: 1px solid rgba(68, 68, 68, 0.2);
}

.footer-copy__count a {
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: rgba(68, 68, 68, 0.6);
}

.footer-copy__count:not(:first-child) {
  margin-left: 20px;
}

.btn-up {
  position: absolute;
  right: 30px;
  bottom: 32px;
  width: 40px;
  height: 40px;
  text-align: center;
  border: none;
  border-radius: 50%;
  background: #ffffff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  appearance: none;
}

.btn-up svg {
  width: 22px;
  height: 15px;
  fill: transparent;
  stroke-width: 2px;
  stroke: rgba(68, 68, 68, 0.6);
}

